import {ImageKey} from "core/types/assets"
import {ms} from "core/types/common"
import {Signals} from "core/ui/animations/signals"
import {Img} from "core/ui/controls/img"

export module SpriteAnimation {
  export function play(img: Img, sprites: ImageKey[], delay: ms, loop = false, startFrame = 0): Phaser.Signal {
    let index = startFrame
    let signal = new Phaser.Signal()
    img.key = sprites[index]
    const interval = img.interval(() => {
      index += 1
      if(index >= sprites.length){
        index = 0
        if(!loop){
          clearInterval(interval)
          signal.dispatch()
        }
      }
      img.key = sprites[index]
    }, delay)
    return signal

  }
}



