import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"
import {Images} from "games/ships/assets"

const durations = {
  ship: 1000
}

export class IntroComponent extends GameComponent {
  positions = Sizes.create({
    from: {x: -300, y: 270},
    to: {y: 270},
  })
  bg = this.addImage(Images.intro)
  item = this.addImage(Images.intro_ship).setPos(this.positions.from)

  playTween() {
    return Tweens.start({
      target: this.item,
      to: {x: Sizes.width},
      duration: durations.ship,
    })
  }

}




