import {CoreColorNumbers} from "core/assets/fonts"
import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Tweens} from "core/ui/animations/tweens"
import {BiValue} from "core/ui/common/positions"
import {Sizes} from "core/ui/common/sizes"
import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {TextPanelComponent} from "core/ui/components/games/gameUI/textPanelComponent"
import {TutorialTextComponent} from "core/ui/components/games/gameUI/tutorialTextComponent"
import {OnInit} from "core/ui/components/onInit"
import {Btn} from "core/ui/controls/btn"
import {Control} from "core/ui/controls/control"
import {BtnDefaultImg} from "core/ui/controls/GraphicsImg"
import {Images, Sounds, Translations} from "games/ships/assets"
import {InstructionComponent} from "games/ships/components/instructionComponent"
import {IntroComponent} from "games/ships/components/introComponent"
import {SeaComponent} from "games/ships/components/seaComponent"
import {Task} from "games/ships/logic"
import {positions} from "games/shop/components/roomDarkComponent"

const durations = {
  sea: 300,
  gull: 10000,
  delay: 100
}

const biValues = BiValue.create({
  scale: 1,
}, {
  scale: 0.75,
})

export class MainComponent extends BaseMainGameComponent implements OnInit {
  bg = this.addImage(Images.bg)
  seaGroup = this.add(new Control())
  sea: SeaComponent

  positions = Sizes.create({
    tutorial: {y: 840},
    taskDescSize: {width: 800, height: 120},
    btnSize: {width: 500, height: 100},
    tutorialBtnShift: 80,
  })

  biPositions = Sizes.bi({
    remember: {x: Sizes.widthNormal / 2, y: 1130},
    taskDesc: {x: Sizes.widthNormal / 2, y: 130},
    tutorialTaskDesc: {x: Sizes.widthNormal / 2, y: 900},
    shift: {x: 105, y: 105},
    seaOffset: {y: 0}
  }, {
    remember: {x: Sizes.widthNormal / 2, y: 1210},
    taskDesc: {x: Sizes.widthNormal / 2, y: 60},
    tutorialTaskDesc: {x: Sizes.widthNormal / 2, y: 1100},
    shift: {x: 130, y: 105},
    seaOffset: {y: 0}
  })

  instruction = this.add(this.createComponent(InstructionComponent))
    .anchorCenterX().setBiPos(this.isTutorial ? this.biPositions.tutorialTaskDesc : this.biPositions.taskDesc).fluentUpdate(x => x.visible = false)

  taskDesc = this.add(new TextPanelComponent({
      size: this.positions.taskDescSize
    })
  ).anchorCenterX().setBiPos(this.biPositions.taskDesc).fluentUpdate(x => {
    x.setProps(this.translate(this.isTutorial ? Translations.tutor1 : Translations.memorize))
    x.visible = false
  })

  rememberBtn = this.add(new Btn(this.translate(Translations.remember), () => this.setInstructionVisible(true, false), undefined, new BtnDefaultImg({
    color: CoreColorNumbers.purple,
    size: this.positions.btnSize
  }))).anchorCenter()
    .fluentUpdate(x => x.visible = false)
    .setBiPos(this.biPositions.remember)

  private tutorial: TutorialTextComponent

  setInstructionVisible(visible: boolean, begin: boolean) {
    this.sea.setShipsVisible(!visible, begin).addOnce(() => {
      CommonAnimations.setVisible(this.taskDesc, !this.isTutorial && !visible, {instantHide: true})
      CommonAnimations.setVisible(this.rememberBtn, !visible, {instantHide: true})
    })
    this.instruction.visible = visible
    if (this.tutorial) {
      this.tutorial.setProps(this.translate(visible ? Translations.tutor2 : Translations.tutor1))
    }
    this.sea.setButtonsEnabled(visible)
  }

  onAnswer(answer: ILastAnswer): void {
    if (answer.status == AnswerStatus.Error) {
      this.setInstructionVisible(false, false)
    }
  }

  onInit(): void {
    this.playBackgroundLoop()
    this.interval(() => this.playSound(Sounds.gull), durations.gull)
    this.introOptions.runOnNewTaskAfter = true
    if (this.isTutorial) {
      this.tutorial = this.add(this.createComponent(TutorialTextComponent).make({}))
      this.taskDesc.visible = false
      this.rememberBtn.y -= this.positions.tutorialBtnShift
    }
    this.startIntro()
    this.onBiValueChange(biValues.scale, (c, v) => {
      this.seaGroup.symmetricScale = v
      this.updateSeaPosition()
    })
  }

  updateSeaPosition(){
    if(!this.sea){
      return
    }
    const fullSize = this.sea.getFullSize()
    const scale = this.current(biValues.scale)
    const shift = this.current(this.biPositions.shift)

    this.sea.updatePos(((Sizes.width - fullSize.width*scale) / 2 + shift.x),
      ((Sizes.height - fullSize.height*scale) / 2 + shift.y) +
      this.current(this.biPositions.seaOffset).y)

  }

  animationNewLevel(task: Task) {
    this.instruction.visible = false
    const prevSea = this.sea
    if (prevSea) {
      prevSea.updatePos(this.sea.x - Sizes.width, this.sea.y)
    }
    this.sea = this.seaGroup.add(this.createComponentWithProps(SeaComponent, task))
    this.updateSeaPosition()
    if (!this.isFirstTask) {
      this.seaGroup.updatePos(Sizes.width, 0)
      Tweens.start({
        target: this.seaGroup,
        duration: durations.sea,
        to: {x: 0},
        easing: Phaser.Easing.Sinusoidal.InOut,
        onComplete: () => {
          this.setInstructionVisible(false, true)
          prevSea.destroy()
        }
      })
    } else {
      this.delay(durations.delay).addOnce(() => {
        this.setInstructionVisible(false, true)
      })
      // this.sea.setButtonsVisible(true)
    }

  }

  introAnimation(): { start: () => void; onComplete: Phaser.Signal } {
    const intro = this.add(this.createComponent(IntroComponent))
    const tween = intro.playTween()
    tween.onComplete.addOnce(() => {
      return intro.destroy()
    })
    return {
      start: () => {
        tween.start()
      },
      onComplete: tween.onComplete
    }
  }

  onNewTask(task: Task) {
    this.instruction.setProps(task)
    if (this.isNewLevel || this.isFirstTask) {
      this.animationNewLevel(task)
    } else {
      if (this.instruction.visible) {
        if (this.isFirstTask || this.isNewLevel) {
          this.setInstructionVisible(false, true)
        } else {
          this.setInstructionVisible(true, false)
        }
      }
    }
  }

}



