import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Signals} from "core/ui/animations/signals"
import {SpriteAnimation} from "core/ui/animations/spriteAnimation"
import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {ImageBtn} from "core/ui/controls/btn"
import {Control} from "core/ui/controls/control"
import {Img} from "core/ui/controls/img"
import {Arrays} from "core/utils/arrays"
import {UniqSeq} from "core/utils/circleSeq"
import {Matrixs} from "core/utils/matrixs"
import {Numbers} from "core/utils/numbers"
import {RandomGenerator} from "core/utils/randomGenerator"
import {tuple} from "core/utils/tuples"
import {Images, Sounds} from "games/ships/assets"
import {Task} from "games/ships/logic"
import * as _ from 'lodash'
import MatrixDirection = Matrixs.MatrixDirection

type Rotation = "top" | "right" | "bottom" | "left"
type ImgType = "sand" | { coastline: { type: Images.CoastlineType, gradient: boolean, left: number, right: number } }
type Rect = { x: number, y: number, width: number, height: number }

function isInside(rect: Rect, [x, y]: [number, number]) {
  return rect.x <= x && x <= rect.x + rect.width && rect.y <= y && y < +rect.y + rect.height
}

const durations = {
  flow: 300,
  shipDelay: 100,
  ship: 1000
}
const angles: Record<Rotation, number> = {
  top: 0,
  right: 90,
  bottom: 180,
  left: 270
}

const flowAngles: {[key in MatrixDirection]: number} = {
  up: 270,
  down: 90,
  left: 180,
  right: 0
}

const maxHeight = 10

export class SeaComponent extends GameComponent<Task> implements OnInit {
  positions = Sizes.create({
    answerMargin: -50,
    icon: [211, 170],
    size: 216
  })
  private ships: Img[] = []

  getFullSize() {
    const units = this.unitSize
    return {
      width: units.width * this.positions.size,
      height: units.height * this.positions.size
    }
  }

  landGroup = this.add(new Control())
  seaGroup = this.add(new Control())
  buttonsGroup = this.seaGroup.add(new Control())
  shipGroup = this.seaGroup.add(new Control())
  private buttons: ImageBtn[] = []
  state = {
    buttonEnabled: false
  }

  createImageKeyFromType(type: ImgType) {
    if (type == "sand") {
      return Images.sand
    } else {
      const fn = Images.Coastline[type.coastline.type]
      const count = type.coastline.type == "join" ? 2 : Images.landCount
      return fn(this.rng.int(count), type.coastline.left, type.coastline.right)
    }

  }

  rng = new RandomGenerator()

  itemUniqSeq = new UniqSeq(Images.landItemList.length)

  createLand(task: Task) {

    this.landGroup.cacheAsBitmap = false
    const [inner, data] = this.generate(task)
    const land = _.flatten(Matrixs.map(data, (item, x, y) => {
      if (item.type) {
        const addTile = (imgKey: string, rotate = true) => {
          const tile = this.makeImage(imgKey)
          tile.anchorCenter()
          if (rotate) {
            tile.angle = item.angle
          }
          tile.updatePos(x * this.positions.size, y * this.positions.size)
          return tile
        }
        const tiles: [Img, boolean][] = []
        tiles.push(tuple(addTile(this.createImageKeyFromType(item.type)), true))
        if (item.type != "sand") {
          // if (item.type.coastline.type == "land" && item.type.coastline.gradient
          //   || item.type.coastline.type == "join") {
          //   tiles.push(tuple(addTile(Images.line_gradient), false))
          // }
        } else {
          if (!Matrixs.onBoard([x, y], Matrixs.getSize(data))) {
            const tile = addTile(this.itemUniqSeq.nextFrom(Images.landItemList), false)
            const signX = Numbers.sign(Numbers.odd(y))
            const signY = Numbers.sign(Numbers.odd(x))
            tile.updatePos(
              tile.x + this.rng.float() * signX * this.positions.size * 0.2,
              tile.y + this.rng.float() * signY * this.positions.size * 0.2)
            tiles.push(tuple(tile, true))
          }
        }
        return tiles
      }
      return []
    }))

    this.landGroup.removeAll(true)
    this.landGroup.addChildren(_.sortBy(_.flatten(land), x => !x[1]).map(x => x[0]))
    this.landGroup.cacheAsBitmap = true
    return inner
  }

  onPropsUpdate(): void {
    this.generateContent()
    this.setButtonsVisible(false)
  }

  generateContent(){
    this.landGroup.removeAll(true)
    this.buttonsGroup.removeAll(true)
    this.shipGroup.removeAll(true)
    const task = this.props
    if(!task){
      return
    }
    const inner = this.createLand(task)
    this.seaGroup.updatePos(inner.x * this.positions.size, inner.y * this.positions.size)
    this.createAnswerButtons(task)
  }

  getWH(task: Task) {
    return this.isPortrait
      ? tuple(task.sea.height, task.sea.width)
      : tuple(task.sea.width, task.sea.height)
  }

  private createAnswerButtons(task: Task) {
    const [width, height] = this.getWH(task)
    const tiles = _.flatten(Matrixs.create(width, height, (x0, y0) => {
      const p = tuple(x0, y0)
      const swapP = this.swapXY(p)
      const isBlock = Arrays.includesWith(task.sea.blocks, swapP)

      const flow = task.sea.flows.find(x => _.isEqual(x[0], swapP))
      const imgKey = isBlock
        ? Images.seaItems[0]
        : flow
          ? Images.flow(0)
          : Images.line
      const isButton = !flow && !isBlock
      const setPosition = (c: Control) => {
        c.anchorCenter()
        c.updatePos(p[0] * this.positions.size, p[1] * this.positions.size)
      }
      if (isButton) {
        const btn = this.addAnswerBehavior(new ImageBtn("",
          this.gameKey(imgKey), () => {
          }), swapP, {dark: true, margin: this.positions.answerMargin})
        btn.button.input.enabled = false
        setPosition(btn)
        return btn
      } else {
        const img = this.addImage(imgKey).anchorCenter()
        if (flow) {
          SpriteAnimation.play(img, Images.flowList.map(x => this.gameKey(x)),
            durations.flow, true)
          img.angle = flowAngles[flow[1]] + (this.isPortrait ? 90 : 0)
        }
        setPosition(img)
        return img
      }

    }))
    this.buttons = tiles.filter((x): x is ImageBtn => x instanceof ImageBtn)
    this.buttonsGroup.removeAll(true)
    this.buttonsGroup.addChildren(tiles)

  }

  setButtonsEnabled(enabled: boolean) {
    this.state.buttonEnabled = enabled
    this.updateButtons()
  }

  updateButtons(){
    this.buttons.forEach(x => x.button.input.enabled = this.state.buttonEnabled)
    if (this.state.buttonEnabled) {
      this.answersAreReady()
    }
  }

  generateCoastline(rect: Rect) {
    const randomEdge = () => this.rng.count(2, 1)
    const start = [1, 1]
    const points = this.rectCoastlinePoints(rect).concat(this.rectCoastlinePointsBottom(rect))
    const tiles = _.range(points.length - 1).reduce((a, i) => {
      const [[prevLeft, _]] = a
      const right = i == points.length - 2 ? start[1] : randomEdge()
      return [[right, prevLeft], ...a]
    }, [start])
    return Arrays.zip(points, tiles).map(([p, t]) => {
      return {
        pos: p.pos, angle: angles[p.rot],
        type: {coastline: {type: p.type, left: t[0], right: t[1], gradient: p.gradient}}
      }
    })
  }

  createPoint(pos: [number, number], rot: Rotation, type: Images.CoastlineType, gradient = true) {
    return {pos, rot, type, gradient}
  }

  swapXY([x, y]: [number, number]) {
    return this.isPortrait ? [y, x] : [x, y]
  }

  animationShips() {
    const task = this.getCurrentTask<Task>()
    const [width, height] = this.getWH(task)
    const [x, y] =
      [-this.positions.size / 2, (Numbers.count(height, 2) - 0.5) * this.positions.size]
    // [this.positions.size*(width/2 - 0.5), (Numbers.count(height, 2) - 0.5) * this.positions.size]
    this.playSound(Sounds.wave)
    const signals = this.ships.map((ship, i) => {
      const {x: x2, y: y2} = ship
      ship.updatePos(-this.seaGroup.x, y)
      // ship.updatePos(x, y)
      return Tweens.start({
        target: ship,
        delay: i * durations.shipDelay,
        duration: durations.ship,
        interpolation: Phaser.Math.catmullRomInterpolation,
        easing: Phaser.Easing.Quadratic.Out,
        to: {x: [x, x2], y: [y, y2]},
        onComplete: () => {
          ship.updatePos(x2, y2)
        }
      }).onComplete
    })
    const complete = Signals.waitAll(signals)
    this.disableInputOn(complete).addOnce(() => {
      this.stopSound(Sounds.wave)
      this.buttonsGroup.visible = true
      CommonAnimations.show(this.buttonsGroup, true)
    })
    return complete
  }

  createShips() {
    const task = this.getCurrentTask<Task>()
    if(!task){
      return Signals.memorize()
    }
    const isFirst = this.ships.length == 0
    this.shipGroup.removeAll(true)
    this.ships = (task.positions.begin).map((p, i) => {
      const [x, y] = this.swapXY(p)
      return this.addImage(Images.boat(i)).updatePos(
        x * this.positions.size,
        y * this.positions.size
      ).anchorCenter()
    })
    this.shipGroup.addChildren(this.ships)
    if (isFirst) {
      return this.animationShips()
    }else{
      return Signals.memorize()
    }
  }

  setButtonsVisible(visible: boolean) {
    this.buttonsGroup.visible = visible
  }

  setShipsVisible(visible: boolean, begin: boolean) {
    this.shipGroup.visible = visible
    return this.createShips()
  }

  rangePoint(v1: number, v2: number) {
    if (v1 > v2) {
      return _.range(v2, v1 + 1).reverse()
    }
    return _.range(v1, v2 + 1)
  }

  rectCoastlinePoints(rect: { x: number, y: number, width: number, height: number }) {
    const h2 = this.unitSize.height / 2

    const leftLine = this.rangePoint(0, rect.x)
      .map((x, i) => this.createPoint([x, h2 - 1], "top", x == rect.x ? "join" : "land", false))
    const rightLine = this.rangePoint(rect.x + rect.width, this.unitSize.width - 1)
      .map((x, i) => this.createPoint([x, h2 - 1], "top", "land", false))

    const line = _.range(rect.x + 1, rect.x + rect.width)
      .map((x, i) => this.createPoint([x, rect.y], "top", i == rect.width - 2 ? "coal" : "land"))
    const left = _.range(rect.y + 1, h2)
      .map((y, i) => this.createPoint([rect.x, y - 1], "left", i == 0 ? "coal" : "land"))
    const right = _.range(rect.y + 1, h2)
      .map((y, i) => this.createPoint([rect.x + rect.width - 1, y], "right", y == h2 - 1 ? "join" : "land"))
    return leftLine.concat(left.reverse(), line, right, rightLine)
  }

  rectCoastlinePointsBottom(rect: { x: number, y: number, width: number, height: number }) {
    const h2 = this.unitSize.height / 2

    const rightLine = this.rangePoint(this.unitSize.width - 1, rect.x + rect.width - 1)
      .map((x, i) => this.createPoint([x, h2], "bottom", x == rect.x + rect.width - 1 ? "join" : "land", false))
    const leftLine = this.rangePoint(rect.x - 1, 0)
      .map((x, i) => this.createPoint([x, h2], "bottom", "land", false))

    const line = _.range(rect.x, rect.x + rect.width - 1)
      .map((x, i) => this.createPoint([x, rect.y + rect.height - 1], "bottom", x == rect.x ? "coal" : "land"))
    const left = _.range(h2 + 1, rect.y + rect.height)
      .map((y, i) => this.createPoint([rect.x, y - 1], "left", i == 0 ? "join" : "land"))
    const right = _.range(h2 + 1, rect.y + rect.height)
      .map((y, i) => this.createPoint([rect.x + rect.width - 1, y], "right", y == rect.y + rect.height - 1 ? "coal" : "land"))
    return rightLine.concat(right, line.reverse(), left.reverse(), leftLine)//left.reverse(), line, right, leftLine)
  }

  // rectCoastlinePoints(rect: {x: number, y: number, width: number, height: number}){
  //   const top = _.range(rect.x + 1, rect.x + rect.width)
  //     .map((x, i) => this.createPoint([x, rect.y], "top", i == rect.width - 2))
  //   const bottom = _.range(rect.x + 1, rect.x + rect.width)
  //     .map((x,i) => this.createPoint([x - 1, rect.y + rect.height - 1], "bottom", i == 0))
  //   const left = _.range(rect.y + 1, rect.y + rect.height)
  //     .map((y,i) => this.createPoint([rect.x, y - 1], "left", i == 0))
  //   const right = _.range(rect.y + 1, rect.y + rect.height)
  //     .map((y,i) => this.createPoint([rect.x + rect.width - 1, y], "right", i == rect.height - 2))
  //   return top.concat(right, bottom.reverse(), left.reverse())
  // }

  getSectionIndex(
    full: { w: number, h: number },
    inner: { w: number, h: number },
    p: { x: number, y: number }) {
    function getIndex(full: number, inner: number, x: number) {
      const start = (full - inner) / 2
      const end = (full + inner) / 2
      return x < start ? 0 : (x >= end ? 2 : 1)
    }

    return getIndex(full.h, inner.h, p.y) * 3 + getIndex(full.w, inner.w, p.x)
  }

  unitSize = {
    width: 0,
    height: 0
  }

  generate(task: Task) {
    // const maxWidth = Math.ceil(Sizes.width/this.positions.size) + 1
    const maxWidth = Math.ceil(Sizes.width / this.positions.size) + 1
    // const maxHeight = Math.ceil(Sizes.height/this.positions.size) + 1
    const [width, height] = this.getWH(task)
    const x = Math.ceil((maxWidth - width) / 2)
    const y = Math.ceil((maxHeight - height) / 2)

    const full = {w: width + x * 2, h: maxHeight}

    this.unitSize = {
      width: full.w,
      height: maxHeight
    }

    const inner = {x, y, width, height}
    const data: { type: ImgType | undefined, angle: number }[][] = Matrixs.create(full.w, full.h, (x, y) => {
      const type: ImgType | undefined = !isInside(inner, [x, y]) ? "sand" : undefined
      return {type, angle: 0}
    })
    const rect = {
      x: inner.x - 1,
      y: inner.y - 1,
      width: inner.width + 2,
      height: inner.height + 2
    }
    const coastline = this.generateCoastline(rect)
    coastline.forEach(x => {
      Matrixs.set(data, x.pos, {type: x.type, angle: x.angle})
    })
    return tuple(inner, data)
  }

  onInit(): void {
    this.onOrientationChange(() => {
      this.generateContent()
      if(this.seaGroup.visible && this.ships.length > 0){
        this.createShips()
      }
      this.updateButtons()
    })
  }

}




