import {Arrays} from "core/utils/arrays"
import {tuple} from "core/utils/tuples"
import {Undef} from "core/utils/undef"
import * as _ from 'lodash'

export module Matrixs {
  export type MatrixDirection = "up" | "down" | "left" | "right"
  export const matrixDirectionList: MatrixDirection[] = ["up", "down", "left", "right"]

  export const directions: Record<MatrixDirection, {x: number, y: number}> = {
    up: {x: 0, y: -1},
    down: {x: 0, y: 1},
    left: {x: -1, y: 0},
    right: {x: 1, y: 0}
  }

  export const oppositeDirections: Record<MatrixDirection, MatrixDirection> = {
    up: "down",
    down: "up",
    left: "right",
    right: "left"
  }


  export function move(p: [number, number], dirs: MatrixDirection[]){
    console.log(p, dirs)
    return dirs.reduce(([x,y],v) => {
      const {x:dx, y:dy} = directions[v]
      return tuple(x + dx, y + dy)
    }, p)
  }

  export function isInside([x,y]: [number, number], {width, height}: {width: number, height: number}){
    return x >= 0 && y >= 0 && x < width && y < height
  }

  export function onBoard([x,y]: [number, number], {width, height}: {width: number, height: number}){
    return x == 0 || y == 0 || x == width - 1 || y == height - 1
  }

  export function getSize<T>(m: T[][]){
    return {width: m.length == 0 ? 0 : m[0].length, height: m.length}
  }

  export function directionIsAvailable(dir: MatrixDirection, p: [number, number],
    size: {width: number, height: number},
    obstacles: [number, number][]) {
    const newP = move(p, [dir])
    return isInside(newP, size) && !Arrays.includesWith(obstacles, newP)
  }

  export function getAvailableDirections(p: [number, number],
    size: {width: number, height: number},
    obstacle: [number, number][]) {
    return matrixDirectionList.filter(dir => directionIsAvailable(dir, p, size, obstacle))
  }

  export function get<T>(m: T[][], [x, y]: [number, number], def?: T) {
    const row = m[y]
    return row ? row[x] : def
  }

  export function set<T>(m: T[][], [x, y]: [number, number], value: T) {
    let row = m[y]
    if (Undef.is(row)) {
      row = []
      m[y] = row
    }
    row[x] = value
  }

  export function compact<T>(m: T[][]) {
    return m.map(_.compact)
  }

  export function create<T>(colCount: number, rowCount: number, fn: (x: number, y: number) => T): T[][] {
    return _.range(rowCount).map(y => _.range(colCount).map(x => fn(x, y)))
  }

  export function map<T, K>(m: T[][], fn: (item: T, x: number, y: number) => K): K[][] {
    return m.map((row, y) => row.map((item, x) => fn(item, x, y)))
  }

  export function intersection<F, S>(f: F[][], s: S[][]) {
    const first: [F, [number, number]][] = []
    const second: [S, [number, number]][] = []
    const both: [F, S, [number, number]][] = []
    _.range(Math.max(f.length, s.length)).forEach(y => {
      _.range(Math.max((f[y] || []).length, (s[y] || []).length)).forEach(x => {
        const p = tuple(x, y)
        const fe = get(f, p)
        const se = get(s, p)
        if (!Undef.is(fe) && !Undef.is(se)) {
          both.push([fe, se, p])
        } else if (!Undef.is(fe)) {
          first.push([fe, p])
        } else if (!Undef.is(se)) {
          second.push([se, p])
        }
      })
    })
    return tuple(first, second, both)
  }
}



