import {createLevelsFromArray} from "core/types/logic"

export const maxShipCount = 7

export interface Level {
  width: number,
  height: number,
  withBlocks: boolean,
  withFlows: boolean,
  shipCount: number,
  instructionLength: number
}

function level(width: number, height: number, shipCount: number,
  instructionLength: number, withBlocks = false, withFlows = false): Level {
  return {width, height, shipCount, instructionLength, withBlocks, withFlows}
}

export const levelsArray = [
  level(3, 2, 1, 1),
  level(3, 3, 2, 1),
  level(3, 3, 2, 2, true, false),
  level(3, 3, 2, 2, false, true),
  level(4, 3, 2, 3),
  level(4, 3, 2, 3, true),
  level(4, 3, 2, 3, false, true),
  level(4, 3, 2, 3, true, true),
  level(4, 3, 3, 1),
  level(4, 4, 3, 3),
  level(4, 4, 3, 3, true, true),
  level(5, 4, 4, 5),
  level(5, 4, 4, 3, true, true),
  level(6, 4, 5, 5),
  level(6, 4, 5, 3, true, true),
  level(6, 4, 6, 5),
  level(6, 4, 6, 3, true, true),
  level(6, 4, maxShipCount, 5),
  level(6, 4, maxShipCount, 3, true, true),
  level(6, 4, maxShipCount, 5, true, true),
]
export const levels = createLevelsFromArray(levelsArray)



