import {GameLogic} from "core/logic"
import {Arrays, RandomGenerator} from "core/utils"
import {UniqSeq} from "core/utils/circleSeq"
import {Matrixs} from "core/utils/matrixs"
import {Numbers} from "core/utils/numbers"
import {tuple} from "core/utils/tuples"
import * as _ from 'lodash'
import {Level} from "./levels"
import MatrixDirection = Matrixs.MatrixDirection

type Position = [number, number]

type Flow = [Position, MatrixDirection]

export type Task = {
  sea: {
    width: number,
    height: number,
    blocks: Position[],
    flows: Flow[],
  },
  shipIndex: number,
  instruction: MatrixDirection[],
  positions: { begin: Position[], end: Position[] },
  answers: Position[]
}

export class Logic extends GameLogic<Task, Level> {
  rng = new RandomGenerator()
  uniqPosSeq = [new UniqSeq(0), new UniqSeq(0)]

  move(p: Position, dirs: MatrixDirection[], blocks: Position[], flows: Flow[]): Position {
    return dirs.reduce(([x, y], v) => {
      const {x: dx, y: dy} = Matrixs.directions[v]
      const newPosition = tuple(x + dx, y + dy)
      if (Arrays.includesWith(blocks, newPosition)) {
        return tuple(x, y)
      }
      const flow = flows.find(x => _.isEqual(x[0], newPosition))
      if (flow) {
        return this.move(newPosition, [flow[1]], blocks, flows)
      }
      return newPosition
    }, p)
  }


  getAllAvailableDirections(position: Position, level: Level,
    restrictPositions: Position[], blocks: Position[], flows: Flow[]){
    return Matrixs.matrixDirectionList.filter(dir => {
      const newPosition = this.move(position, [dir], blocks, flows)
      return Matrixs.isInside(newPosition, level)
        && !Arrays.includesWith(restrictPositions, newPosition)
    })
  }

  generateInstruction(level: Level, position: Position, otherShips: Position[],
    blocks: Position[], flows: Flow[]): [MatrixDirection[], Position] {

    const restrictPositions = blocks.concat(flows.map(x => x[0]), otherShips)
    return _.range(level.instructionLength).reduce(([dirs, p]) => {
      const allAvailable = this.getAllAvailableDirections(p, level,
        restrictPositions, blocks, flows)
      if (allAvailable.length != 0) {
        const lastDir = _.last(dirs)
        const available = !lastDir || allAvailable.length == 1
          ? allAvailable
          : allAvailable.filter(x => x != Matrixs.oppositeDirections[lastDir])
        const dir = this.rng.sample(available)
        dirs.push(dir)
        return tuple(dirs, this.move(p, [dir], blocks, flows))
      } else {
        return tuple(dirs, p)
      }
    }, tuple([] as MatrixDirection[], position))
  }

  generateBlocksAndFlows(level: Level) {
    // const count = (withItem: boolean, withSecond: boolean) =>
    //   !withItem ? 0 : (level.width == 3 || (level.width == 4 && withSecond) ? 1 : 2)

    const positions = Arrays.uniqWith(Arrays.cartesianProduct(
      [1, level.width - 2], [1, level.height - 2]))
    const count = level.withBlocks && level.withFlows
      ? Numbers.count(positions.length, 2)
      : positions.length
    const [firstPositions, secondPositions] = this.rng.split(positions, count)
    const [blocks, flowPositions] = level.withBlocks
      ? tuple(firstPositions, secondPositions)
      : tuple([], firstPositions)
    const getFlowDirections = (p: Position) => Matrixs.matrixDirectionList.filter(dir => {
      const nextPosition = Matrixs.move(p, [dir])
      return !Arrays.includesWith(positions, nextPosition)
    })
    const flows = level.withFlows ? flowPositions
      .map(x => tuple(x, this.rng.sample(getFlowDirections(x)))) : []
    return tuple(blocks, flows)
  }

  generateBeginPositions(level: Level) {
    const [blocks, flows] = this.generateBlocksAndFlows(level)
    const restrictPositions = blocks.concat(flows.map(x => x[0]))
    this.uniqPosSeq.forEach(x => x.reset())
    const nextPosition = (): Position => {
      const p = tuple(this.uniqPosSeq[0].next(level.width), this.uniqPosSeq[1].next(level.height))
      if (Arrays.includesWith(restrictPositions, p)
        || this.getAllAvailableDirections(p, level, restrictPositions, blocks, flows).length == 0) {
        return nextPosition()
      }
      restrictPositions.push(p)
      return p
    }
    return tuple(_.range(level.shipCount).map(i => nextPosition()), blocks, flows)
  }

  nextTask(level: Level) {
    const prevTask = this.getPrevTask()
    const usePrevTask = prevTask && !this.isNewLevel
    const {width, height} = level
    const [[positionBeginList, blocks, flows], shipIndex] = prevTask && usePrevTask
      ? tuple(tuple(prevTask.positions.end, prevTask.sea.blocks, prevTask.sea.flows), (prevTask.shipIndex + 1) % level.shipCount)
      : tuple(this.generateBeginPositions(level), 0)
    const positionEndList = prevTask && usePrevTask ? prevTask.positions.end : positionBeginList
    const [[positionBegin], otherPositions] =
      Arrays.partition(positionEndList, (x, i) => {
        return i == shipIndex
      })
    const [instruction, positionEnd] = this.generateInstruction(level, positionBegin, otherPositions, blocks, flows)
    const positions = {
      begin: positionBeginList,
      end: Arrays.updateAt(positionEndList, shipIndex, () => positionEnd)
    }
    const answers = [positionEnd]
    return {
      sea: {
        width,
        height,
        flows,
        blocks
      },
      shipIndex,
      instruction,
      positions,
      answers
    }
  }

}





