import {CoreColors, CoreFonts} from "core/assets/fonts"
import {ImagesMetaData} from "core/types"
import * as _ from 'lodash'

export module Translations {
  export const memorize = "memorize"
  export const remember = "remember"
  export const tutor1 = "tutor1"
  export const tutor2 = "tutor2"
}

export module Sounds {

  export const bg = "bg"
  export const gull = "gull"
  export const wave = "wave"

  export const keys: string[] = [
    bg,
    gull,
    wave
  ]
}

export module Fonts {

  export const instruction = {
    fontSize: 100, font: CoreFonts.Arial, fill: CoreColors.white, align: "center"
  }
}

export module Images {
  export const landCount = 4
  export const bg = "bg"
  export const arrow = "arrow"
  export const intro = "intro"
  export const intro_ship = "intro_ship"
  export const sand = "sand"
  export const circle = "circle"
  export const line = "line"
  export const landTypeList = ["mount", "tree"]
  export const seaItems = ["ships_tree", "ships_mount"]
  export const line_gradient = "line_gradient"
  export const land = (index: number, left: number, right: number) => `land_${index}_${left}_${right}`
  export const landItem = (type: "mount" | "stone" | "tree", index: number) => `land_${type}_${index}`
  export const landItemList = _.range(3).map(i => landItem("stone", i)).concat(
    _.range(3).map(i => landItem("tree", i)))
  export const coal = (index: number, left: number, right: number) => `coal_${index}_${left}_${right}`
  export const join = (index: number, left: number, right: number) => `join_${index}_${left}_${right}`
  export const boat = (index: number) => `boat_${index}`
  export const flow = (index: number) => `flow_${index}`
  export const flowList = _.reverse(_.range(4).map(flow))
  export type CoastlineType = "coal" | "land" | "join"
  export const Coastline: Record<CoastlineType,
    (index: number, left: number, right: number) => string> = {
    coal, land, join
  }
  export const data: ImagesMetaData = {
    sprites: []
  }

}




