import {IGameMetaData} from "core/logic"
import {LevelOptions, ScoreOptions} from "core/types"
import {Images, Sounds} from "./assets"
import {MainComponent} from "./components/mainComponent"
import {levels, levelsArray} from "./levels"
import {Logic} from "./logic"

export const gameId = "ships"
const scoreOptions: ScoreOptions = {
  scoreAnswer: 100,
  scoreBonus: 0,
  scoreBonusPerLevel: 5
}

const levelOptions: LevelOptions = {
  levels,
  levelsSeqCounts: levelsArray.map((x, i) => ({
    seqCount: (x.shipCount) * 3,
    maxLevel: i + 1
  }))
}

export const metaData: IGameMetaData = {
  gameId,
  title: "Word search",
  logic: Logic,
  component: MainComponent,
  scoreOptions,
  levelOptions,
  darkBg: false,
  images: Images.data,
  sounds: Sounds.keys,
  atlasCount: 2,
  tutorialTaskCount: 3,
  size: {
    width: 2048,
    height: 1280,
    heightMin: 1152
  }
}



