import {BiValue} from "core/ui/common/positions"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {PanelComponent} from "core/ui/components/games/gameUI/panelComponent"
import {Control} from "core/ui/controls/control"
import {Images} from "games/ships/assets"
import {Task} from "games/ships/logic"

const up = -90
const down = 90
const left = 180
const right = 0

const biValues = BiValue.create({
  arrows: {
    up,
    down,
    right,
    left,
  }
}, {
  arrows: {
    "left": up,
    "right": down,
    "up": left,
    "down": right,
  }
})

const scale = 0.4

export class InstructionComponent extends GameComponent<Task> {

  positions = Sizes.create({
    taskDescSize: {width: 700, height: 160},
    arrows: {x: 130},
    text: {x: 420, y: 80},
    margin: {x: 10}
  })

  bg = this.add(new PanelComponent()).setProps({size: this.positions.taskDescSize})
  group = this.add(new Control()).anchorCenter().setPos(this.bg.center())

  onPropsUpdate() {
    this.onBiValueChange(biValues.arrows, (c, v) => {
      const task = this.props
      this.group.removeAll(true)

      this.group.add(this.makeImage(Images.circle)).anchorCenter()

      const ship = this.group.add(this.makeImage(Images.boat(task.shipIndex)))
        .anchorCenter()
        .fluentUpdate(x => x.symmetricScale = scale)

      const arrowsGroup = this.group.add(new Control()).setPos(this.positions.arrows)
      const arrows = task.instruction.map(arrow => {
        return arrowsGroup.add(this.makeImage(Images.arrow)).anchorCenter()
          .fluentUpdate(x => x.angle = v[arrow])
      })
      Sizes.tableRow(arrows, this.positions.margin)
      this.group.updatePivot()

    })
    this.updatePivot()
  }

}



